* {
  margin: 0;
  padding-inline: 0;
  margin-inline: 0;
}

html {
  font-size: 10px;
}

.app-wrapper {
  width: 100%;
  min-height: 100vh;
  background-color: #000000;
}

.App {
  display: grid;
  grid-template-columns: 150px auto;
  grid-template-rows: 50px auto;
  grid-auto-flow: row;
  grid-template-areas: 
    "header header"
    "nav main";
  height: inherit;
  margin-left: auto;
  margin-right: auto;
  width: 80%;
  max-width: 1200px;
  min-height: 100vh;
  background-color: #fafafa;
  align-self: center;
}

.app_header {
  grid-area: header;
  font-family: Verdana;
  font-weight: bold;
  font-size: 1.6rem;
  text-align: center;
}

.app-nav {
  background-color: lawngreen;
  padding-inline-start: 1rem;
}

.app-article {
  grid-area: main;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.app-footer {
  grid-area: footer;
  font-family: Verdana;
  font-weight: bold;
  font-size: 1rem;
  text-align: right;
  margin: auto 0.5rem 0.5rem auto;
}

@media screen and (max-width: 768px) {
  .App {
    display: flex;
    flex-direction: column;
    height: inherit;
    margin-left: auto;
    margin-right: auto;
    width: 100%;
    max-width: 1200px;
    background-color: #fafafa;
    align-self: center;
  }

  .app-nav-list {
    display: flex;
    flex-wrap: wrap;
  }
}
