.app-article-button, .app-article-button:visited {
    padding: 10px;
    margin: 5px;
    background-color: indigo;
    border-radius: 10px;
    font-size: 1.1rem;
    font-family: Verdana;
    font-weight: 700;
    color: #fafafa;
    cursor: pointer;
    user-select: none;
    text-align: center;
}

.app-article-button:hover {
    background-color: goldenrod;
    color: #fafafa;
}

.app-article-button:active {
    background-color: transparent;
    color: #000000;
}