.nav-list-item {
    display: block;
    margin: 0.5rem 0 0.5rem 0;
    padding-right: 1rem;
    list-style-type: none;
    font-family: Tahoma;
    font-size: 1.2rem;
    font-weight: 700;
    margin: 0.5rem 0 0.5rem 0;
    text-align: left;
}

.nav-list-item a, .nav-list-item a:visited {
    color: #000000;
}

.nav-list-item a:hover {
    color: goldenrod;
}
